import React, { Component } from 'react'
import './styles.css'
import {connect} from 'react-redux'
import { Link, withRouter, Redirect  } from 'react-router-dom'
import { endpoint } from "../../constant"
import { fetchCart } from "../../store/actions/cart";
import {PulseLoader} from 'react-spinners'
import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'
import {authAxios} from '../../utils'
import Form from 'react-bootstrap/Form'
import Badge from 'react-bootstrap/Badge'
import { Button } from 'react-bootstrap'
import Modal from 'react-bootstrap/Modal'
import { GrTrash } from 'react-icons/gr'
import { FaRupeeSign } from "react-icons/fa";
import {
  Message,
  Select
} from "semantic-ui-react";



class CouponForm extends Component {
  state = {
    code: "",
    loading: 'false',
    error: null,
  };

  handleChange = e => {
    this.setState({
      code: e.target.value
    });
  };

  handleSubmit = e => {
    const { code } = this.state;
    this.props.handleAddCoupon(e, code);
    this.setState({ code: "" });
  };
  

  render() {
    const { code } = this.state;
    return (
      <React.Fragment>
        <Form onSubmit={this.handleSubmit} style={{display: 'flex', flexDirection: 'row'}}>
          <Form.Group>
            <input
              placeholder="Enter a coupon.."
              value={code}
              onChange={this.handleChange}
            />
          </Form.Group>
          <Button style={{marginLeft: '10px'}} variant="danger" type="submit">Add</Button>
        </Form>
      </React.Fragment>
    );
  }
}



export class Order extends Component {
  constructor(props) {
    super(props)
    
    this.state = {
    order: [],
    error: null,
    formVisible: false,
    loading: 'false',
    billingAddresses: '',
    userID: '',
    code: '',
    mainorder: [],
    formData: {
      apartment_address: "",
      street_address: "",
      zip: "",
      id: "",
    },
    saving: false,
    success: 'false',
    selectedShippingAddress: "",
    redio1: '' 
  }
}
    componentDidMount(){
        this.handleFetchOrder()
        this.handleFetchUserID()
        this.handleFetchBillingAddresses()
    }

    handleToggleForm = () => {
      const {  formVisible } = this.state
      this.setState({
        formVisible: !formVisible
      })
    }

    handleFetchUserID = () => {
      authAxios
          .get(`${endpoint}/user/`)
          .then(res => {
            this.setState({ userID: res.data })
          })
    }

    handleFetchOrder = () => {
        this.setState({ loading: true });
        authAxios
          .get(`${endpoint}/order-summary/`)
          .then(res => {
            this.setState({ order: res.data, loading: false })
            const {order} = this.state
            this.setState({ mainorder: order.order_items })
            this.setState({loading: false})
          })
          .catch(err => {
            if (err.response.status === 404) {
              this.setState({
                error: "You currently do not have an order",
                loading: false
              });
            } else {
              this.setState({ error: err, loading: false });
            }
          });
      };

      handleFetchBillingAddresses = () => {
        this.setState({ loading: true });
        authAxios
          .get(`${endpoint}/addresses/`)
          .then(res => {
            this.setState({
              billingAddresses: res.data.map(a => {
                return {
                  key: a.id,
                  text: `${a.street_address}, ${a.apartment_address}`,
                  zip: `${a.zip}`,
                  value: a.id
                };
              }),
            });
          })
          .catch(err => {
            this.setState({ error: err, loading: false });
          });
      };
     

      handleCreateAddress = () => {
        const { formData, userID } = this.state;
        const userid = userID.userID
        authAxios
          .post(`${endpoint}/addresses/create/`, {
            ...formData,
            user: userid,
          })
          .then(res => {
            this.setState({
              saving: false,
              success: true,
              formData: { default: false }
            });
            this.props.callback();
          })
          .catch(err => {
            this.setState({ error: err });
          });
      };

      handleChange = e => {
        const { formData } = this.state;
        const updatedFormdata = {
          ...formData,
          [e.target.name]: e.target.value
        };
        this.setState({
          formData: updatedFormdata
        });
      };

      handleSubmit = e => {
        e.preventDefault();
        const { formData } = this.state
        console.log(formData)
      };


    addressSubmit = ev => {
      ev.preventDefault();
      this.setState({ loading: true });
        const {
          selectedShippingAddress
        } = this.state;
        console.log(selectedShippingAddress)
        authAxios
          .post(`${endpoint}/addressselect/`, {
            selectedShippingAddress,
          })
          .then(res => {
            this.setState({ loading: false, success: true });
          })
          .catch(err => {
            this.setState({ loading: false, error: err });
          });
    }

    handleRemoveItem = itemID => {
      authAxios
        .delete(`${endpoint}/order-items/${itemID}/delete/`)
        .then(res => {
          this.props.refreshCart()
          this.handleFetchOrder()
        })
        .catch(err => {
          this.setState({ error: err });
        });
    };

    renderVariations = order_item => {
      let text = "";
      order_item.item_variations.forEach(iv => {
        text += `${iv.variation.name}: ${iv.value}, `;
      });
      return text;
    };

    handleSelectChange = (e, { name, value }) => {
      this.setState({ [name]: value })
    }

    handleAddCoupon = (e, code) => {
      e.preventDefault()
      this.setState({ loading: true })
      authAxios
        .post(`${endpoint}/apply-coupon/`, {code})
        .then(res => {
          this.setState({ loading: false })
          this.handleFetchOrder()
        })
        .catch(err => {
          this.setState({ error: err, loading: false })
        })
    }
    

    render() {
        const { error, formData, formVisible, success, saving, order, billingAddresses,mainorder,loading,selectedShippingAddress } = this.state
        const {isAuthenticated} = this.props
        if (!isAuthenticated) {
          return <Redirect to='/login' />
        }
        return (
            <div>
                <Header />
                {loading ? (
                <PulseLoader size={30} margin={5} color='#36D7B7' />
               ): (
                 <div>
                {mainorder.length > 0 ? 
                (
                  <div className='cart-div'>
                  <div className='wishlist-div'>
                    <h2>Your Wishlist</h2>
                  {mainorder.map((order_item) => {
                    return (
                       <div className='order-card' key={order_item.id}>
                         <div className='order-card-image'>
                           {order_item.item.image != null ?
                           (
                            <Link to={`/pcdetails/${order_item.item.id}`}>
                            <img src={order_item.item.image} alt='orderi'></img>
                            </Link>
                           ): (
                             <Link to={`/produuctdetail/${order_item.product.id}`}>
                            <img src={order_item.product.image} alt='orderi'></img>
                            </Link>
                           )}
                         </div>
                        <h5>{order_item.item.title}{order_item.product.title}</h5>
                        {/* <p>{this.renderVariations(order_item)}</p> */}
                        <p>{order_item.item.discount_price}{order_item.product.price}</p>
                        <Button variant="secondary" style={{ float: "right", cursor: "pointer", border: 'none',backgroundColor: '#F0F0F0' }}
                                onClick={() => this.handleRemoveItem(order_item.id)}><GrTrash />
                        </Button>                                    
                      </div>
                    )
                  })}
                </div>
                <div className='total'>
                  <h5>PRICE DETAILS</h5>
                  <div className='total-body'>
                    <p>Delivery Charges: Free</p>
                    <CouponForm handleAddCoupon={(e, code) => this.handleAddCoupon(e, code)} />
                     {order.coupon && <Badge pill variant="success">current coupon: {order.coupon.code} for ${order.coupon.amount}</Badge>}
                     <p>Total Amount:<FaRupeeSign size='20px'/>{order.total}</p>
                     <Link to="/checkout" style={{fontSize: '50px'}}><Button variant="outline-danger">Checkout</Button></Link>
                  </div>
                </div>
                </div>
                ) : (
                  <Modal.Dialog>
                    <Modal.Header closeButton>
                      <Modal.Title style={{ color: 'black'}}>Empty </Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                      <p style={{ color: 'black'}}>Add somthing in your wishlist</p>
                    </Modal.Body>

                    <Modal.Footer>
                      <Link to='/gamingpc'>Add</Link>
                    </Modal.Footer>
                  </Modal.Dialog>
                )}
       
                  <div className='wishlist-div'>
                  <h2>Select a shipping address</h2>
                  <Select
                      name="selectedShippingAddress"
                      value={selectedShippingAddress}
                      clearable
                      options={billingAddresses}
                      selection
                      onChange={this.handleSelectChange}
                    />
                    <Button variant="danger" onClick={this.addressSubmit}>Delivery here</Button>
                    <Button variant="danger" onClick={this.handleToggleForm}>Add a shipping address</Button>
                    </div>


                  <div className='wishlist-div'>
                  {formVisible && (
                      <Form onSubmit={this.handleCreateAddress} success={success} error={error}>
                        <Form.Group>
                        <Form.Label>House address</Form.Label>
                        <Form.Control
                          required
                          name="street_address"
                          placeholder="Street address"
                          onChange={this.handleChange}
                          value={formData.street_address}
                        />
                        </Form.Group>
                        <Form.Group>
                        <Form.Control
                          required
                          name="apartment_address"
                          placeholder="Apartment address"
                          onChange={this.handleChange}
                          value={formData.apartment_address}
                        />
                        </Form.Group>
                        <Form.Group>
                        <Form.Control
                          required
                          name="zip"
                          placeholder="Zip code"
                          onChange={this.handleChange}
                          value={formData.zip}
                        />
                        <Form.Text className="text-muted">
                          We'll never share your email with anyone else.
                        </Form.Text>
                        </Form.Group>
                        {success && (
                          <Message success header="Success!" content="Your address was saved" />
                        )}
                        {error && (
                          <Message
                            error
                            header="There was an error"
                            content={JSON.stringify(error)}
                          />
                        )}
                        <Button variant="success" type="submit" disabled={saving} loading={saving}>
                          Save
                        </Button>
                      </Form>
                  )}
                  </div>
                  </div>
               )}  

                <Footer />
            </div>
        )
    }
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.auth.isAuthenticated
  }
}
const mapDispatchToProps = dispatch => {
  return {
    refreshCart: () => dispatch(fetchCart())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Order))
