import React, { Component } from 'react'
import axios from 'axios'
import {authAxios} from '../../utils'
import { endpoint } from "../../constant";

import './styles.css'
import { GiDeathJuice } from "react-icons/gi";
import { BiRupee } from "react-icons/bi";

export class Ram extends Component {
    state = {
        partsdata: [],
        select: 'R',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }
    memorySubmit = slug => {
        this.setState({loading:true})
        authAxios.post(`${endpoint}/add-memory/`, {slug})
        .then(res => {
            this.props.refreshCart();
            this.setState({ loading: false })
        })
        .catch(err => {
            this.setState({error: err,loading:false })
        })
    }

    render() {
        let memory = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div className='frame1'>
                 <div className='tab1'>
                 {
                    memory &&
                    memory.map( memory =>{
                        return(
                            <div className='memory' key={memory.id}>
                                <img src={memory.image} alt='g'></img>
                                <div>
                                <p>{memory.title}</p>
                                <p><BiRupee size='30px' />{memory.price}</p>
                                </div>
                                <button onClick={() => this.memorySubmit(memory.slug)} className='feb-button'><GiDeathJuice />Select</button>
                            </div>
                            )
                        })
                  }
            </div>
            </div>
        )
    }
}

export default Ram
