import React, { Component } from 'react'
import {
    Message,
    Divider,
  } from "semantic-ui-react"
import Form from 'react-bootstrap/Form'
import Card from 'react-bootstrap/Card'
import { Button } from 'react-bootstrap'
import { endpoint } from "../../constant"
import {authAxios} from '../../utils'




const UPDATE_FORM = "UPDATE_FORM";
const CREATE_FORM = "CREATE_FORM";
class AddressForm extends React.Component {
    state = {
        error: null,
        loading: false,
        userID: {},
        formData: {
          apartment_address: "",
          street_address: "",
          zip: "",
          id: "",
        },
        saving: false,
        success: false
      };

    componentDidMount() {
    this.handleFetchUserID()
    const { address, formType } = this.props;
    if (formType === UPDATE_FORM) {
      this.setState({ formData: address });
    }
    } 
    handleFetchUserID = () => {
      authAxios
          .get(`${endpoint}/user/`)
          .then(res => {
            this.setState({ userID: res.data })
          })
    }

    handleCreateAddress = () => {
        const { formData, userID } = this.state
        const userid = userID.userID 
          authAxios
            .post(`${endpoint}/addresses/create/`, {
              ...formData,
              user: userid,
          })
          .then(res => {
            this.setState({
              saving: false,
              success: true,
              formData: { default: false }
            });
            this.props.callback();
          })
          .catch(err => {
            this.setState({ error: err });
          });
      };
    
      handleUpdateAddress = () => {
        const { formData } = this.state;
        authAxios
          .put(`${endpoint}/addresses/${formData.id}/update/`, {
            ...formData,
          })
          .then(res => {
            this.setState({
              saving: false,
              success: true,
              formData: { default: false }
            });
            this.props.callback();
          })
          .catch(err => {
            this.setState({ error: err });
          });
      };


    handleChange = e => {
        const { formData } = this.state;
        const updatedFormdata = {
          ...formData,
          [e.target.name]: e.target.value
        };
        this.setState({
          formData: updatedFormdata
        });
      };

    handleSubmit = e => {
        this.setState({ saving: true });
        e.preventDefault();
        const { formType } = this.props;
        if (formType === UPDATE_FORM) {
          this.handleUpdateAddress();
        } else {
          this.handleCreateAddress();
        }
      };

    render(){
        const { error, formData, success, saving } = this.state
        return(
            <Form onSubmit={this.handleSubmit} success={success} error={error}>
                   <Form.Group>
                   <Form.Label>House address</Form.Label>
                    <Form.Control
                      required
                      name="street_address"
                      placeholder="Street address"
                      onChange={this.handleChange}
                      value={formData.street_address}
                    />
                    </Form.Group>
                    <Form.Group>
                    <Form.Control
                      required
                      name="apartment_address"
                      placeholder="Apartment address"
                      onChange={this.handleChange}
                      value={formData.apartment_address}
                    />
                    </Form.Group>
                    <Form.Group>
                    <Form.Control
                      required
                      name="zip"
                      placeholder="Zip code"
                      onChange={this.handleChange}
                      value={formData.zip}
                    />
                    <Form.Text className="text-muted">
                      We'll never share your email with anyone else.
                    </Form.Text>
                    </Form.Group>
                    {success && (
                      <Message success header="Success!" content="Your address was saved" />
                    )}
                    {error && (
                      <Message
                        error
                        header="There was an error"
                        content={JSON.stringify(error)}
                      />
                    )}
                    <Button variant="primary" type="submit" disabled={saving} loading={saving}>
                      Save
                    </Button>
                  </Form>
        )
    }
}  

class Shipping extends Component {
  state = {
     Addresses: [],
     userID: '',
     formVisible: false,
  }

  componentDidMount() {
      this.handleFetchAddresses()
      this.handleFetchUserID()
    }

    handleToggleForm = () => {
      const {  formVisible } = this.state
      this.setState({
        formVisible: !formVisible
      })
    }

    handleFetchUserID = () => {
      authAxios
          .get(`${endpoint}/user/`)
          .then(res => {
            this.setState({ userID: res.data });
          })
    }  
    handleDeleteAddress = addressID => {
      authAxios
        .delete(`${endpoint}/addresses/${addressID}/delete/`)
        .then(res => {
          this.handleCallback();
        })
        .catch(err => {
          this.setState({ error: err });
        });
    };
  
    handleSelectAddress = address => {
      this.setState({ selectedAddress: address });
    };
  
    handleFetchAddresses = () => {
      this.setState({ loading: true });
      authAxios
        .get(`${endpoint}/addresses/`)
        .then(res => {
          this.setState({ Addresses: res.data, loading: false });
        })
        .catch(err => {
          this.setState({ error: err, loading: false });
        });
    };

    handleCallback = () => {
      this.handleFetchAddresses();
      this.setState({ selectedAddress: null });
    };
    
    
  render() {
      const { Addresses, selectedAddress, userID, formVisible } = this.state;
      return (
          <div>
              <div className='container'>
                <h2>Your Address</h2>
              {Addresses.map(a => {
                  return (
                    <Card key={a.id}>
                      <Card.Header>{a.id}</Card.Header>
                      <Card.Body>
                        <Card.Title>{a.street_address}</Card.Title>
                        <Card.Text>
                         {a.apartment_address}, {a.zip}
                        </Card.Text>
                        <Button variant="outline-warning" onClick={() => this.handleSelectAddress(a)}>Update</Button>
                      <Button variant="outline-danger" onClick={() => this.handleDeleteAddress(a.id)}>Delete</Button>
                      </Card.Body>
                    </Card>
                  )}
              )}
              <Button  onClick={this.handleToggleForm}>Add new address</Button>
              {formVisible && (
                  <AddressForm />
              )}
              {Addresses.length > 0 ? <Divider /> : null}
                  {selectedAddress === null ? (
                  <AddressForm
                      formType={CREATE_FORM}
                      userID={userID}
                      callback={this.handleCallback}
                  />
                  ) : null}
                  {selectedAddress && (
                  <AddressForm
                      userID={userID}
                      address={selectedAddress}
                      formType={UPDATE_FORM}
                      callback={this.handleCallback}
                  />
                  )}
                  </div>
          </div>
      )
  }
}


export default Shipping

