import Axios from "axios";
import React from "react";
import { endpoint } from "../../constant";
import {authAxios} from '../../utils'

class Checkout extends React.Component {

  state = {
    order: "",
    mainorder: "",
    loading: 'false',
    error: null,
  };
  componentDidMount(){
    this.handleFetchOrder()
}

  handleFetchOrder = () => {
    this.setState({ loading: true });
    authAxios
      .get(`${endpoint}/order-summary/`)
      .then(res => {
        this.setState({ order: res.data, loading: false })
        const {order} = this.state
        this.setState({ mainorder: order.order_items })
        this.setState({loading: false})
      })
      .catch(err => {
        if (err.response.status === 404) {
          this.setState({
            error: "You currently do not have an order",
            loading: false
          });
        } else {
          this.setState({ error: err, loading: false });
        }
      });
  };



  handlePaymentSuccess = async (response) => {
    try {
      let bodyData = new FormData();

      // we will send the response we've got from razorpay to the backend to validate the payment
      bodyData.append("response", JSON.stringify(response));

      await Axios({
        url: `${endpoint}/razorpay/payment/success/`,
        method: "POST",
        data: bodyData,
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      })
        .then((res) => {
          console.log("Everything is OK!");
        })
        .catch((err) => {
          console.log(err);
        });
    } catch (error) {
      console.log(console.error());
    }
  };

  // this will load a script tag which will open up Razorpay payment card to make transactions
  // loadScript = () => {
  //     const script = document.createElement("script");
  //     script.src = "https://checkout.razorpay.com/v1/checkout.js";
  //   };

  showRazorpay = async () => {
    // const res = await this.loadScript();
    
    let bodyData = new FormData();

    // we will pass the amount and product name to the backend using form data
    bodyData.append("amount", 657);
    bodyData.append("name", 'jdshdhj');
    const data = await Axios({
      url: `${endpoint}/Razorpaycli/`,
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      data: bodyData,
    }).then((res) => {
      return res;
    });

    // in data we will receive an object from the backend with the information about the payment
    //that has been made by the user

    var options = {
      key_id: 'ID rzp_test_YC00OtQGjYOJgt', // in react your environment variable must start with REACT_APP_
      key_secret: '3mfxnCEqjCdazWBWYiz9GH8M',
      amount: data.data.payment.amount,
      currency: "INR",
      name: "Org. Name",
      description: "Test teansaction",
      image: "", // add image url
      order_id: data.data.payment.id,
      handler: function (response) {
        // we will handle success by calling handlePaymentSuccess method and
        // will pass the response that we've got from razorpay
        this.handlePaymentSuccess(response)
      },
      prefill: {
        name: "User's name",
        email: "User's email",
        contact: "User's phone",
      },
      notes: {
        address: "Razorpay Corporate Office",
      },
      theme: {
        color: "#3399cc",
      },
    };

    var rzp1 = new window.Razorpay(options);
    rzp1.open();
  };
  render(){
  return (
    <div className="container" style={{ marginTop: "20vh" }}>
     <h1>Payment page</h1>
      <button onClick={this.showRazorpay} className="btn btn-primary btn-block">
        Pay with razorpay
      </button>
    </div>
  )
  }
}

export default Checkout