import React, { Component } from 'react'
import axios from 'axios'
import {authAxios} from '../../utils'
import { endpoint } from "../../constant";

import './styles.css'
import { GiDeathJuice } from "react-icons/gi";
import { BiRupee } from "react-icons/bi";

export class Processor extends Component {
    state = {
        partsdata: [],
        select: 'C',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }
    processorSubmit = slug => {
        this.setState({loading:true})
        authAxios.post(`${endpoint}/add-processor/`, {slug})
        .then(res => {
            this.props.refreshCart();
            this.setState({ loading: false })
        })
        .catch(err => {
            this.setState({error: err,loading:false })
        })
    }
    render() {
        let processor = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div className='frame1'>
            <div className='tab1'>
            {
               processor &&
               processor.map( processor =>{
                   return(
                        <div className='processor' key={processor.id}>
                            <img src={processor.image} alt='g'></img>
                            <div>
                            <p>{processor.title}</p>
                            <p><BiRupee size='30px' />{processor.price}</p>
                            </div>
                            <button onClick={() => this.processorSubmit(processor.slug)} className='feb-button'><GiDeathJuice />Select</button>
                        </div>
                       )
                   })
             }
       </div>
       </div>
        )
    }
}

export default Processor
