import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { connect } from 'react-redux';
import * as actions from './store/actions/auth';


import Aboutus from './pages/aboutus/Aboutus';
import Landing from './pages/landing/Landing';
import Login from './pages/login/Login'
import Pcdetails from './pages/Pcdetails/Pcdetails'
import Pcpage from './pages/pcpage/Pcpage'
import Pccomponents from './pages/pcComponents/Pccomponents'
import Productdetails from './pages/productdetails/Productdetails'
import Registration from './pages/signup/Registration'
import Recovery from './pages/Recovery/Recovery';
import Profile from './pages/account/Profile'
import Custom from './pages/Custom/Custom'
import Create from './pages/create/Create'
import Order from './pages/order/Order'
import Customize from './pages/customize/Customize'
import Checkout from './pages/checkout/Checkout'
import Resetpassword from './pages/Resetpassword/Resetpassword';
import Activate from './pages/activate/Activate';
import Facebook from './pages/login/Facebook'
import Google from './pages/login/Google'
import Bld from './pages/whatisbld/Bld'
import Support from './pages/support/Support'
import Warranty from './pages/warranty/Warranty'

import Graphics from './component/computercomp/Graphics'
import Motherb from './component/computercomp/Motherb'
import Memory from './component/computercomp/Memory'



class App extends Component {
  componentDidMount() {
    this.props.onTryAutoSignup();
  }
  render(){
    return (
      <Router>
        <div className="app">
        <Switch>
              <Route path="/login" >
                <Login />
                </Route>
              <Route path="/recovery">
                <Recovery />
                </Route>
              <Route path="/registration">
                <Registration />
              </Route>
              <Route path="/account">
                <Profile />
                </Route>
                <Route path="/create">
                <Create />
                </Route>
                <Route path="/custom">
                <Custom/>
                </Route>
                <Route exact path="/mother-board" component={Motherb} />
                <Route exact path="/memory" component={Memory} />
                <Route exact path="/graphic-card" component={Graphics} />
                <Route exact path="/warranty" component={Warranty} />
                <Route exact path="/support" component={Support} />
                <Route exact path="/bld" component={Bld} />
                <Route exact path="/aboutus" component={Aboutus} />
                <Route exact path="/facebook" component={Facebook} />
                <Route exact path="/google" component={Google} />  
                <Route exact path="/pcdetails/:id" component={Pcdetails} />
                <Route exact path="/produuctdetail/:id" component={Productdetails} />
                <Route exact path="/order" component={Order} />
                <Route exact path="/customize/:id" component={Customize} />
                <Route exact path='/password/reset/confirm/:uid/:token' component={Resetpassword} />
                <Route exact path='/activate/:uid/:token' component={Activate} />
                <Route exact path="/checkout" component={Checkout} />
                <Route exact path="/gamingpc" component={Pcpage} />
                <Route exact path="/pccomponents" component={Pccomponents} />
                <Route exact path="/" component={Landing} />
            </Switch>
        </div>
      </Router>
  )
  }
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.auth.isAuthenticated
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onTryAutoSignup: () => dispatch(actions.checkAuthenticated())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
