import React, { Component } from 'react'
import axios from 'axios'
import { withRouter } from 'react-router-dom'
import {connect} from 'react-redux'
import { fetchCart } from "../../store/actions/cart"
import { authAxios } from '../../utils'
import { endpoint } from "../../constant";
import {PulseLoader} from 'react-spinners'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Tab from 'react-bootstrap/Tab'
import Nav from 'react-bootstrap/Nav'

import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'
import './styles.css'
import { BiRupee } from "react-icons/bi";
import { AiFillControl } from "react-icons/ai";

export class Productdetails extends Component {
    state = {
        detaildata: {},
        loading: false,
    }
    componentDidMount(){
        const id = this.props.match.params.id;
        axios.get(`${endpoint}/pccomponent/${id}`)
        .then(res => {
            this.setState({
                detaildata: res.data
            })
            this.setState({loading: true})
        })
    }
    handleAddToCart = slug => {
         authAxios.post(`${endpoint}/add-to-wish/`, {slug})
         .then(res => {
            this.props.refreshCart()
            this.setState({ loading: false })
         })
        .catch(err => {
             this.setState({error: err,loading:false })
         })
    }
    render() {
        return (
            <div>
                <Header />
                {!this.state.loading ? (
                <PulseLoader size={30} margin={5} color='#36D7B7' />
                ): (
                <div>
                <Row className='component-detail-main'>
                    <Col lg={6} sm={12} className='component-detail-image'>
                      <img src={this.state.detaildata.image} alt='cpudetail'></img>
                    </Col>
                    <Col lg={6} sm={12} className='component-detail'>
                    <h2>{this.state.detaildata.title}</h2>
                    <h3><BiRupee size='40px' />{this.state.detaildata.price}</h3>
                    <h4><AiFillControl size='40px' />{this.state.detaildata.Warranty}</h4>
                    <p>{this.state.detaildata.description}</p>
                         <button type='button' onClick={() => this.handleAddToCart(this.state.detaildata.slug)} className='vgbutton'>Order</button>
                    </Col>
                </Row>

                <Tab.Container defaultActiveKey="first">
                <Row className='custom-page-row'>
                    <Col sm={12} lg={12}>
                    <Nav className='custom-react-tab'>
                        <Nav.Item>
                        <Nav.Link eventKey="first" className='custom-react-div'>Specifications</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                        <Nav.Link eventKey="second" className='custom-react-div'>Description</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    </Col>
                    <Col sm={12} lg={12}>
                    <Tab.Content>
                        <Tab.Pane eventKey="first" className='custom-react-panel'>
                        <div className='customize-card'>
                         <div className='customize-card-detail'>
                         <p className='pcdetail-para-1'>Brand:</p>
                         <p>{this.state.detaildata.brand}</p>
                         </div>
                         {this.state.detaildata.busstandard && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Bus Standard:</p>
                            <p>{this.state.detaildata.busstandard}</p>
                            </div>
                         )}
                         {this.state.detaildata.engine && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Engine:</p>
                            <p>{this.state.detaildata.engine}</p>
                            </div>
                         )}
                         {this.state.detaildata.factor && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Factor:</p>
                           <p>{this.state.detaildata.factor}</p>
                           </div>
                         )}
                         {this.state.detaildata.frequency && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Frequency:</p>
                           <p>{this.state.detaildata.frequency}</p>
                           </div>
                         )}
                         {this.state.detaildata.gpuclock && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Clock:</p>
                           <p>{this.state.detaildata.gpuclock}</p>
                           </div>
                         )}
                         {this.state.detaildata.memory && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Memory:</p>
                           <p>{this.state.detaildata.memory}</p>
                           </div>
                         )}
                         {this.state.detaildata.frequency && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Model Id:</p>
                           <p>{this.state.detaildata.frequency}</p>
                           </div>
                         )}
                         {this.state.detaildata.modelname && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Model Name:</p>
                            <p>{this.state.detaildata.modelname}</p>
                            </div>
                         )}
                         {this.state.detaildata.powersupply && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Power Supply:</p>
                           <p>{this.state.detaildata.powersupply}</p>
                           </div>
                         )}
                         {this.state.detaildata.Series && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Series:</p>
                            <p>{this.state.detaildata.Series}</p>
                            </div>
                         )}
                         {this.state.detaildata.threads && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Threads:</p>
                            <p>{this.state.detaildata.threads}</p>
                            </div>
                         )}
                         {this.state.detaildata.manufacture && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Manufacture:</p>
                            <p>{this.state.detaildata.manufacture}</p>
                            </div>
                         )}
                         {this.state.detaildata.cardinterface && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Card Interface:</p>
                            <p>{this.state.detaildata.cardinterface}</p>
                            </div>
                         )}
                         {this.state.detaildata.compatability && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Compatability:</p>
                            <p>{this.state.detaildata.compatability}</p>
                            </div>
                         )}
                         {this.state.detaildata.graphictransfer && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Graphic Transfer:</p>
                            <p>{this.state.detaildata.graphictransfer}</p>
                            </div>
                         )}
                         {this.state.detaildata.videomemory && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Video Memory:</p>
                            <p>{this.state.detaildata.videomemory}</p>
                            </div>
                         )}
                         {this.state.detaildata.weight && (
                           <div className='customize-card-detail'>
                           <p className='pcdetail-para-1'>Weight:</p>
                           <p>{this.state.detaildata.weight}</p>
                           </div>
                         )}
                         {this.state.detaildata.ramsize && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Ram Size:</p>
                            <p>{this.state.detaildata.ramsize}</p>
                            </div>
                         )}
                         {this.state.detaildata.rammemorytec && (
                            <div className='customize-card-detail'>
                            <p className='pcdetail-para-1'>Ram Memory Tec:</p>
                            <p>{this.state.detaildata.rammemorytec}</p>
                            </div>
                         )}
                        </div>
                        </Tab.Pane>
                        <Tab.Pane eventKey="second" className='custom-react-panel'>
                        <p>Boost Clock / Memory Speed 1860 MHz / 8 Gbps
                            4GB GDDR5
                            DisplayPort x 2 / HDMI x 1
                            Twin Frozr 7 Thermal Design

                            TORX Fan 3.0 - Dispersion fan blade: Steep curved blade accelerating the airflow. - Traditional fan blade: Provides steady airflow to massive heat sink below.
                            Mastery of Aerodynamics: The heatsink is optimized for efficient heat dissipation, keeping your temperatures low and performance high.
                            Zero Frozr technology: Stopping the fan in low-load situations, keeping a noise-free environment.
                            Dragon Center

                            A consolidated platform that offers all software including MYSTIC LIGHT functionality for your MSI Gaming product.</p>
                        </Tab.Pane>
                    </Tab.Content>
                    </Col>
                </Row>
                </Tab.Container>
                </div>
                )}
                <Footer />
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
      isAuthenticated: state.auth.isAuthenticated
    };
  };
const mapDispatchToProps = dispatch => {
    return {
      refreshCart: () => dispatch(fetchCart())
    };
  };

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Productdetails))  
