import React, { Component } from 'react'
import axios from 'axios'
import {authAxios} from '../../utils'
import {endpoint} from '../../constant'

import './styles.css'
import { GiDeathJuice } from "react-icons/gi";
import { BiRupee } from "react-icons/bi";
import config1 from '../../assets/config1.png'
import config2 from '../../assets/config2.png'
import config4 from '../../assets/config4.png'
import config5 from '../../assets/config5.png'
import config6 from '../../assets/config6.png'


export class Gpu extends Component {
    state = {
        partsdata: [],
        select: 'G',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }

    gpuSubmit = slug => {
        this.setState({loading:true})
        authAxios.post(`${endpoint}/add-gpu/`, {slug})
        .then(res => {
            this.props.refreshCart();
            this.setState({ loading: false })
        })
        .catch(err => {
            this.setState({error: err,loading:false })
        })
    }

    render() {
        let gpu = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div className='frame1'>
            <div className='tab1'>
                 {
                    gpu &&
                    gpu.map( gpu =>{
                        return(
                            <div className='gpu' key={gpu.id}>
                                <img src={gpu.image} alt='g'></img>
                                <div>
                                <p>{gpu.title}</p>
                                <p><BiRupee size='30px' />{gpu.price}</p>
                                </div>
                                <button onClick={() => this.gpuSubmit(gpu.slug)} className='feb-button'><GiDeathJuice />Select</button>
                            </div>
                            )
                        })
                  }
            </div>
            <div className='tab1-right'>
                <div className='tab1-image'>
                    <img src={config1} alt='configphoto'></img>
                    <img src={config2} alt='configphoto'></img>
                </div>
                <div className='tab1-image2'>  
                    <img src={config4} alt='configphoto'></img>
                    <img src={config5} alt='configphoto'></img>
                    <img src={config6} alt='configphoto'></img>
                </div>
            </div>    
        </div>
        )
    }
}

export default Gpu
