import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import Header from '../layout/Header'
import Footer from '../layout/Footer/Footer'
import axios from 'axios'
import { endpoint } from "../../constant";
import { BiRupee } from "react-icons/bi";
import './styles.css'

export class Memory extends Component {
    state = {
        partsdata: [],
        select: 'R',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }
    render() {
        let memorydata = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div>
                <Header />
            <div style={{ backgroundColor: '#1F1D29'}}>
                <div className='pc-components-head'>
                <div className='pc-components-head1'>
                <h1>THE H SERIES, OUR VISION FOR A MODERN PC</h1>
                <button className='vg2button'>LEARN MORE</button>
                </div>
                </div>
                <div className='component-tab-nav'>
                 <Link to='/pccomponents' className='component-tab-link'>PRODUCT</Link>
                </div>
            <div className='Product-div'>
            {
               memorydata &&
               memorydata.map( memorydata =>{
                   return(
                        <div className='product-card' key={memorydata.id}>
                            <Link to={`/produuctdetail/${memorydata.id}`}>
                        <img src={memorydata.image} alt='processorimages'></img>
                        </Link>
                        <div className='product-card-info'>
                            <h2>{memorydata.title}</h2>
                            <p><BiRupee size='30px' />{memorydata.price}</p>
                        </div>
                        </div>
                   )                       
                })
          }

        </div>
        </div>
        <Footer />
        </div>
        )
    }
}

export default Memory
