import React from 'react'
import "./styles.css";
import {Link} from 'react-router-dom'
import { Button } from 'react-bootstrap'
import axios from 'axios'
import { productListURL } from "../../../constant";
import { FaRupeeSign } from 'react-icons/fa';
import {PulseLoader} from 'react-spinners'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form'
import 'aos/dist/aos.css';




class Product extends React.Component {
    state = {
        filterVisible: false,
        gamingpc: [],
        loading: false,
    }

    componentDidMount(){
        axios.get(productListURL)
        .then(res => {
            this.setState({
                gamingpc: res.data
            })
            this.setState({loading: true})
        })
    }
    handleToggleFilter = () => {
        const {  detailVisible } = this.state
        this.setState({
            detailVisible: !detailVisible
        })
      }
    render(){
    return (
        <div className='product-title-main'>
            <h1>Gaming PC</h1>
            {!this.state.loading ? (
                <PulseLoader size={30} margin={5} color='#36D7B7' />
            ): (
                <Row>
                <Col lg={2} sm={12}>
                <div className='filter-button'>
                 <Button variant="danger" onClick={this.handleToggleFilter}>Show filter</Button>
                 </div>
                 {this.state.detailVisible && (
                  <div className='product-left-filter'>
                   <h3>Category</h3>
                   <Form>
                   <div className="form-check">
                       <input type="checkbox" id="intel" />Intel
                   </div>
                   <div className="form-check">
                       <input type="checkbox" id="ryzen" />Ryzen
                   </div>
                   <div className="form-check">
                       <input type="checkbox" id="workstation" />Workstation
                   </div>
                   </Form>
                   <h3 style={{marginTop: '60px'}}>Price</h3>
                   <Form>
                   <div className="form-check">
                       <input type="checkbox" id="intel" />Intel
                   </div>
                   <div className="form-check">
                       <input type="checkbox" id="ryzen" />Ryzen
                   </div>
                   <div className="form-check">
                       <input type="checkbox" id="workstation" />Workstation
                   </div>
                   </Form>
                   </div>
                    )}
                    </Col>
                    <Col lg={10} sm={12}>
                    <div className='product'>
                    {
                        this.state.gamingpc &&
                        this.state.gamingpc.map( gamingpc =>{
                            return(
                                <Link to={`/pcdetails/${gamingpc.id}`}><div className='cards' key={gamingpc.id}>
                                            <div className='photo'>
                                            <img src={gamingpc.image} alt='pic' />
                                            </div>
                                            <div className='producttitle'>
                                            <h2>{gamingpc.title} ({gamingpc.processor})</h2>
                                            <h5><FaRupeeSign />{gamingpc.price}</h5>
                                            </div>
                                    </div>
                                </Link>
                                )
                            })
                    }
                    </div>
                    </Col>
                   </Row>

            )}
            
        </div>
      )
  }
}

export default Product
