import React, { Component } from 'react'
import axios from 'axios'
import {authAxios} from '../../utils'

import './styles.css'
import { GiDeathJuice } from "react-icons/gi";
import { BiRupee } from "react-icons/bi";
import { endpoint } from "../../constant";

export class Motherboard extends Component {
    state = {
        partsdata: [],
        select: 'M',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }

    motherboardSubmit = slug => {
        this.setState({loading:true})
        authAxios.post(`${endpoint}/add-motherboard/`, {slug})
        .then(res => {
            this.props.refreshCart();
            this.setState({ loading: false })
        })
        .catch(err => {
            this.setState({error: err,loading:false })
        })
    }
    render() {
        let motherbord = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div className='frame1'>
                 <div className='tab1'>
                 {
                    motherbord &&
                    motherbord.map( motherbord =>{
                        return(
                            <div className='motherbord' key={motherbord.id}>
                                <img src={motherbord.image} alt='g'></img>
                                <div>
                                <p>{motherbord.title}</p>
                                <p><BiRupee size='30px' />{motherbord.price}</p>
                                </div>
                                <button onClick={() => this.motherboardSubmit(motherbord.slug)} className='feb-button'><GiDeathJuice />Select</button>
                            </div>
                            )
                        })
                  }
            </div>
            </div>
        )
    }
}

export default Motherboard
