import React from 'react'
import axios from 'axios'
import { Link } from "react-router-dom";
import Header from '../../component/layout/Header';
import Footer from '../../component/layout/Footer/Footer'
import {PulseLoader} from 'react-spinners'
import './pcdetails.css'
import Row from 'react-bootstrap/Row'
import { Button } from 'react-bootstrap'
import Col from 'react-bootstrap/Col'
import detail from '../../assets/detail.png'
import detail2 from '../../assets/detail2.png'
import { endpoint } from "../../constant";

import { FiServer } from "react-icons/fi";
import { GrVirtualMachine } from "react-icons/gr";
import { GrUbuntu } from "react-icons/gr";
import { FiCpu } from "react-icons/fi";
import { FaRupeeSign } from "react-icons/fa";
import { BiWifi } from "react-icons/bi";
import { FiBluetooth} from "react-icons/fi";
import { FiAirplay } from "react-icons/fi";
import { GrTroubleshoot } from "react-icons/gr";
import { RiRemoteControlFill } from "react-icons/ri";



class Pcdetails extends React.Component{
    state = {
        gamingpc: {},
        loading: false,
        detailVisible: false,
    }
    componentDidMount(){
        const id = this.props.match.params.id;
        axios.get(`${endpoint}/${id}`)
        .then(res => {
            this.setState({
                gamingpc: res.data
            })
            this.setState({loading: true})
        })
    }
    handleToggleForm = () => {
        const {  detailVisible } = this.state
        this.setState({
            detailVisible: !detailVisible
        })
      }
    
    render(){
        console.log(this.state.gamingpc)
        return(
            <div style={{backgroundColor: '#1f1d29'}}>
                <Header />
                {!this.state.loading ? (
                <PulseLoader size={30} margin={5} color='#36D7B7' />
            ): (
                <div>
                <div className='sec1'>
                    <Row>
                    <Col lg={7} sm={12}>
                    <div className='detail-main'>
                    <h2>{this.state.gamingpc.title}</h2><br></br>
                    <p>{this.state.gamingpc.description}</p>
                    <h4><FaRupeeSign size='40px'/>{this.state.gamingpc.price}</h4>  
                    <h4>THE POWER TO CREATE</h4>    
                    <Link to={`/customize/${this.state.gamingpc.id}`}><button className='vgbutton'>customize</button></Link>    
                    </div>           
                    </Col>
                    <Col lg={5} sm={12}>
                    <img src={this.state.gamingpc.image} alt='detailmain'></img>
                    </Col>
                    </Row>
                </div>

                <div className='defh'>
                    <div className='defh-item'>
                       <FiCpu size='100px'/>
                       <h3>CPU</h3>
                        <p>{this.state.gamingpc.processor}</p> 
                    </div>
                    <div className='defh-item'>
                       <GrUbuntu size='100px'/>
                       <h3>RAM</h3>
                        <p>{this.state.gamingpc.memory}</p>
                    </div>
                    <div className='defh-item'>
                       <GrVirtualMachine size='100px'/>
                        <h3>Graphics</h3>
                        <p>{this.state.gamingpc.gpu}</p>
                    </div>
                    <div className='defh-item'>
                      <FiServer size='100px'/>
                       <h3>MOTHERBOARD</h3>
                        <p>{this.state.gamingpc.motherboard} </p> 
                    </div>
                </div>
                <div className='defh2'>
                    <p><BiWifi size='50px'/>WIFI</p>
                    <p><FiBluetooth size='50px' />Bluetooth</p>
                    <p><FiAirplay size='50px' />Stream Ready</p>
                    <p><GrTroubleshoot size='50px' />VR Ready</p>
                    <p><RiRemoteControlFill size='50px' />USB-C</p>
                </div>


                <div className='hide-button'>
                <Button variant="danger" onClick={this.handleToggleForm}>Show Specs</Button>
                </div>
            
            {this.state.detailVisible && (
            <div className='pcdetail-hide'>
                <div className='pcdetail-hide1'>
                   <div className='pcdetail-hide2'>
                    <p className='pcdetail-para'>Case:</p>
                    <p>{this.state.gamingpc.case}</p>
                   </div>
                   <div className='pcdetail-hide2'>
                    <p className='pcdetail-para'>Processor:</p>
                    <p>{this.state.gamingpc.processor}</p>
                   </div>
                   <div className='pcdetail-hide2'>
                   <p className='pcdetail-para'>Gpu:</p>
                   <p>{this.state.gamingpc.gpu}</p>
                   </div>
                   <div className='pcdetail-hide2'>
                   <p className='pcdetail-para'> Motherboard:</p>    
                   <p>{this.state.gamingpc.motherboard}</p>
                   </div>
                   <div className='pcdetail-hide2'>
                    <p className='pcdetail-para'>Ram:</p>
                   <p>{this.state.gamingpc.memory}</p>
                   </div>
                   </div>
                <div className='pcdetail-hide1'>
                   <div className='pcdetail-hide2'>
                    <p className='pcdetail-para'>Cpucooler:</p>
                   <p>{this.state.gamingpc.cpucooler}</p>
                   </div>
                   <div className='pcdetail-hide2'>
                       <p className='pcdetail-para'>Powersupply:</p>
                       <p>{this.state.gamingpc.powersupply}</p>
                   </div>
                   <div className='pcdetail-hide2'><p className='pcdetail-para'>Storage:</p> <p>{this.state.gamingpc.storage}</p></div>
                  <div className='pcdetail-hide2'><p className='pcdetail-para'>Software:</p><p>{this.state.gamingpc.software}</p></div>
                   <div className='pcdetail-hide2'><p className='pcdetail-para'>Warranty:</p><p>{this.state.gamingpc.warranty}</p></div>
                </div>
            </div>    
            )}

            <Row className='detail-box'>
               <Col sm={12} lg={6} className='detail-box-left'>
                   <div className='detail-box-text'>
                   <h1>SMALL BUT POWERFUL</h1>
                   <p>With an Intel i9-9900K processor and an Nvidia RTX 2070 GPU, the H1 Mini PC is a pint-sized powerhouse. This build is the perfect solution for gaming enthusiasts who wish for a small case without compromise.</p>
                   </div>
               </Col>
               <Col sm={12} lg={6} className='detail-box-right'>
               <img src={detail2} alt='pcdetail'></img>
               </Col>
            </Row>
            <Row className='detail-box'>
               <Col sm={12} lg={6} className='detail-box2-left'>
               <img src={detail} alt='pcdetail2'></img>
               </Col>
               <Col sm={12} lg={6}className='detail-box2-right'>
                   <div className='detail-box-text'>
                   <h1>COOLEST CASE ON THE BLOCK</h1>
                   <p>The dual-chamber airflow of the H1 is thermally designed to allow the CPU, GPU, and PSU to pull in fresh air, and exhaust hot air without affecting temperatures of neighboring components.</p>
                   </div>
               </Col>
            </Row>
            </div>
            )}
            <Footer />
        </div>
        )
    }
}


export default Pcdetails