import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import Header from '../layout/Header'
import Footer from '../layout/Footer/Footer'
import { endpoint } from "../../constant";
import { BiRupee } from "react-icons/bi";
import './styles.css'

export class Graphics extends Component {
    state = {
        partsdata: [],
        select: 'G',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }
    render() {
        let gpudata = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div>
                <Header />
                <div style={{ backgroundColor: '#1F1D29'}}>
                <div className='pc-components-head'>
                <div className='pc-components-head1'>
                <h1>THE H SERIES, OUR VISION FOR A MODERN PC</h1>
                <button className='vg2button'>LEARN MORE</button>
                </div>
                </div>
                <div className='component-tab-nav'>
                 <Link to='/pccomponents' className='component-tab-link'>PRODUCT</Link>
                </div>
                    <div className='Product-div'>
                    {
                    gpudata &&
                    gpudata.map( gpudata =>{
                        return(
                                <div className='product-card' key={gpudata.id}>
                                <Link to={`/produuctdetail/${gpudata.id}`}>
                                <img src={gpudata.image} alt='processorimages'></img>
                                </Link>
                                <div className='product-card-info'>
                                    <h2>{gpudata.title}</h2>
                                    <p><BiRupee size='30px' />{gpudata.price}</p>
                                </div>
                                </div>
                        )                       
                        })
                }
            </div>
        </div>
        <Footer />
        </div>
        )
    }
}

export default Graphics
