import { authAxios } from '../../utils'
import { endpoint } from "../../constant";
import { CART_START,CART_SUCCESS,CART_FAIL } from './actionType';

export const cartStart = () => {
    return {
        type: CART_START
    }
}

export const cartSuccess = data => {
    return {
        type: CART_SUCCESS,
        data
    }
}
export const cartFail = error => {
    return {
        type: CART_FAIL,
        error: error
    }
}

export const fetchCart = () => {
    return dispatch => {
        dispatch(cartStart())
        authAxios.get(`${endpoint}/order-summary/`)
        .then(res => {
            dispatch(cartSuccess(res.data));
        })
        .catch(err => {
            dispatch(cartFail(err))
        })
    }
}
