import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux';
import Tab from 'react-bootstrap/Tab'
import Tabs from 'react-bootstrap/Tabs'
import Account from '../../component/customer/Account'
import Orderlist from '../../component/customer/Orderlist'
import Shipping from '../../component/customer/Shipping'
import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'

const Profile = props => {
    const [key, setKey] = useState('home')

    if (props.isAuthenticated == null) {
        return <Redirect to='/' />
    }
        return (
            <div>
              <Header />
                <Tabs id="controlled-tab-example"
                      activeKey={key}
                      onSelect={(k) => setKey(k)}
                      >
                    <Tab eventKey="home" title="Profile">
                        <Account />
                    </Tab>
                    <Tab eventKey="profile" title="Address">
                        <Shipping />
                    </Tab>
                    <Tab eventKey="contact" title="Orders">
                        <Orderlist />
                    </Tab>
                </Tabs>
              <Footer />
            </div>
        )
}

const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.auth.isAuthenticated,
        loading: state.loading,
        error: state.error
    }
  }

export default connect(mapStateToProps)(Profile);