import React from 'react'
import './styles.css'
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { fetchCart } from "../../store/actions/cart";
import Burger from './Drawer/Burger'
import { RiShoppingCartFill } from 'react-icons/ri';



class Header extends React.Component{
    render(){
    const {isAuthenticated,cart} = this.props
    return (
        <div className='navb'>
            <nav>
                <h1 className='brand'><Link to="/"><strong>RI<span>GG</span>ED</strong></Link></h1>
                <Burger />
                { isAuthenticated && (
                  <Link to='/order'><p style={{ color: 'white', marginRight:'70px', marginTop: '10px'}}><RiShoppingCartFill size={30} />{cart !== null ? cart.order_items.length : 0 }</p></Link>
                )}
          </nav>
        </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    cart: state.cart.shoppingCart
  }
}
const mapDispatchToProps = dispatch => {
  return {
    fetchCart: () => dispatch(fetchCart())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header))