import React from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import { endpoint } from "../../constant";
import { BiRupee } from "react-icons/bi";
import {PulseLoader} from 'react-spinners'
import './styles.css'

class Cpu extends React.Component {
    state = {
        partsdata: [],
        select: 'C',
        loading: false,
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
            this.setState({loading: true})
        })
    }
    render(){
        let cpudata = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        console.log(cpudata)
    return (
        <div>
            {!this.state.loading ? (
                <PulseLoader size={30} margin={5} color='#36D7B7' />
            ): (
        <div className='Product-div'>
            {
               cpudata &&
               cpudata.map( cpudata =>{
                   return(
                        <div className='product-card' key={cpudata.id}>
                        <Link to={`/produuctdetail/${cpudata.id}`}>
                        <img src={cpudata.image} alt='processorimages'></img>
                        </Link>
                        <div className='product-card-info'>
                            <h2>{cpudata.title}</h2>
                            <p><BiRupee size='30px' />{cpudata.price}</p>
                        </div>
                    </div>
                   )                       
                })
          }

        </div>
            )}
    </div>    
    )
    }
}

export default Cpu
