import React, { Component } from 'react'
import {connect} from 'react-redux'
import { withRouter } from 'react-router-dom'
import {Select,Form} from "semantic-ui-react"
import {PulseLoader} from 'react-spinners'
import { fetchCart } from "../../store/actions/cart"
import { authAxios } from '../../utils'
import { endpoint } from "../../constant"
import './styles.css'
import axios from 'axios'
import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import { GiDeathJuice } from "react-icons/gi";
import { FaRupeeSign } from "react-icons/fa";

export class Customize extends Component {
    state = {
        pc: [],
        formData: {},
        loading: false,
    }
    componentDidMount(){
        const id = this.props.match.params.id;
        axios.get(`${endpoint}/customize/${id}`)
        .then(res => {
            this.setState({
                pc: res.data
            })
            this.setState({loading: true})
        })
    }

    handleAddToCart = slug => {
        const { formData } = this.state
        const variations = this.handleFormatData(formData);
         authAxios.post(`${endpoint}/add-to-cart/`, {slug, variations})
         .then(res => {
            this.props.refreshCart()
            this.setState({ loading: false })
         })
        .catch(err => {
             this.setState({error: err,loading:false })
         })
    }

    handleFormatData = formData => {
        return Object.keys(formData).map(key => {
          return formData[key];
        })
      }

    handleChange = (e, { name, value }) => {
        const { formData } = this.state;
        const updatedFormData = {
          ...formData,
          [name]: value
        };
        this.setState({ formData: updatedFormData });
      };
    render() {
        const {formData} = this.state
        // const {isAuthenticated} = this.props
        // if (!isAuthenticated) {
        //   return <Redirect to='/login' />
        // }
        return (
            <div style={{ backgroundColor: '#1f1d29', color: 'white'}}>
                <Header />
                {!this.state.loading ? (
                <PulseLoader size={30} margin={5} color='#36D7B7' />
                ): (
                <Row>
                <Col lg={6} sm={12}>
                    <Image className='customize-image' src={this.state.pc.image} alt='rewu' />
                    </Col>
                <Col lg={6} sm={12} className='customize-card'>
                         <h1>{this.state.pc.title}</h1>
                         <div className='customize-card-detail'>
                         <p className='pcdetail-para'>Chipset:</p>
                         <p>{this.state.pc.category}</p>
                         </div>
                         <div className='customize-card-detail'>
                         <p className='pcdetail-para'>Cpu:</p>
                         <p>{this.state.pc.processor}</p>
                         </div>
                         <div className='customize-card-detail'>
                         <p className='pcdetail-para'>Gpu:</p>
                         <p>{this.state.pc.gpu}</p>
                         </div>
                         <div className='customize-card-detail'>
                         <p className='pcdetail-para'>Motherboard:</p>
                         <p>{this.state.pc.motherboard}</p>
                         </div>
                         <div className='customize-card-detail'>
                         <p className='pcdetail-para'>Memory:</p>
                         <p>{this.state.pc.memory}</p>
                         </div>
                        <Form>
                        {this.state.pc.variations && (
                            this.state.pc.variations.map(v => {
                                const name = v.name.toLowerCase();
                                return (
                                    <Form.Field key={v.id}>
                                    <Select
                                        style={{color:'red'}}
                                        name={name}
                                        onChange={this.handleChange}
                                        placeholder={`Select a ${name}`}
                                        fluid
                                        selection
                                        options={v.item_variations.map(item => {
                                        return {
                                            key: item.id,
                                            text: item.value,
                                            value: item.id
                                        };
                                        })}
                                        value={formData[name]}
                                    />
                                    </Form.Field>
                                );
                            })
                        )}
                        </Form>
                       <p style={{fontSize:'40px', color: '#8F9BA5'}}>Subtotal:<spam style={{fontSize:'30px', color: '#d3323f'}} ><FaRupeeSign size='30px'/>{this.state.pc.price}rs</spam></p>
                       <button className='vgbutton'
                                onClick={() => this.handleAddToCart(this.state.pc.slug)}><GiDeathJuice />add to wishlist</button>
                    </Col>
                </Row>
                )}
                <Footer />
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
      isAuthenticated: state.auth.isAuthenticated
    };
  };
const mapDispatchToProps = dispatch => {
    return {
      refreshCart: () => dispatch(fetchCart())
    };
  };

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Customize))  
