import React from 'react'
import './styles.css'
import { AiOutlinePlayCircle } from 'react-icons/ai';

import { authAxios } from '../../../utils'
import { endpoint } from "../../../constant"
import config from '../../../assets/config.png'

class Startbld extends React.Component {
    state = {
        error: null,
        loading: false,
    }

    handleCreatePc = () => {
         authAxios.post(`${endpoint}/create-pc/`)
         .then(res => {
             this.setState({ loading: false })
         })
        .catch(err => {
             this.setState({error: err,loading:false })
         })
    }
    render() {
    return (
        <div className='startbld-main'>
            <img src={config} alt='configphoto' />
            <h1>COMING SOON</h1>
            <div className='midbutton'>
                <button onClick={() => this.handleCreatePc()} type='button'>Start BLD</button>
            </div>
            <p><AiOutlinePlayCircle size={28} style={{color: 'white'}} />Watch how RIGGED Computers are built</p>
        </div>
    )
    }
}

export default Startbld
