import React, { useState } from 'react';
import './styles.css'
import axios from 'axios'
import { localhost } from "../../constant"
import { Link, Redirect } from 'react-router-dom'
import { Formik, Form } from 'formik';
import { TextField } from './TextField';
import * as Yup from 'yup'
import { connect } from 'react-redux';
import { signup } from '../../store/actions/auth'
import { GrGoogle } from 'react-icons/gr';
import { GrFacebook } from 'react-icons/gr';
import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'



const Registration = props => {
  const [accountCreated, setAccountCreated] = useState(false);
  const validate = Yup.object({
    first_name: Yup.string()
      .max(20, 'Must be 20 characters or less')
      .required('Required'),
    email: Yup.string()
      .email('Email is invalid')
      .required('Email is required'),
    password: Yup.string()
      .min(8, 'Password must be at least 8 charaters')
      .required('Password is required'),
    re_password: Yup.string()
      .oneOf([Yup.ref('password'), null], 'Password must match')
      .required('Confirm password is required'),
  })

  const onFinish = (values) => {
    props.onAuth(
      values.first_name,
      values.email,
      values.password,
      values.re_password
  )
  setAccountCreated(true);
  }
  if (props.isAuthenticated) {
    return <Redirect to='/' />
  }
  if (accountCreated) {
    return <Redirect to='/login' />
  }

  const continueWithGoogle = async () => {
    try {
        const res = await axios.get(`${localhost}/auth/o/google-oauth2/?redirect_uri=${localhost}/google`)

        window.location.replace(res.data.authorization_url);
    } catch (err) {

    }
  };

  const continueWithFacebook = async () => {
    try {
        const res = await axios.get(`${localhost}/auth/o/facebook/?redirect_uri=${localhost}/facebook`)

        window.location.replace(res.data.authorization_url);
    } catch (err) {

    }
};


  return (
    <div>
      <Header />
    <div className='login-container'>
       <Formik
          initialValues={{
            first_name: '',
        email: '',
        password: '',
        re_password: ''
          }}
          validationSchema={validate}
          onSubmit={onFinish}
        >
          {formik => (
            <div>
              <Form className="sign-in-form">
              <h2 class="title">Check your Email</h2>
                <TextField label="Name" name="first_name" type="text" />
                <TextField label="Email" name="email" type="email" />
                <TextField label="Password" name="password" type="password" />
                <TextField label="Confirm Password" name="re_password" type="password" />
                <button className="btn btn-dark mt-3" type="submit">Register</button>
                <button className='btn btn-danger mt-3' onClick={continueWithGoogle}>
                  <GrGoogle />
                  &nbsp;&nbsp;Continue With Google
                </button>
                <button className='btn btn-primary mt-3' onClick={continueWithFacebook}>
                  <GrFacebook />
                 &nbsp;&nbsp;Continue With Facebook
                </button>
                <Link to='/login'>Login</Link>
              </Form>
            </div>
          )}
        </Formik>
      </div>
      <Footer />
      </div>
  )
}

const mapStateToProps = (state) => {
  return {
      isAuthenticated: state.auth.isAuthenticated,
      loading: state.loading,
      error: state.error
  }
}

const mapDispatchToProps = dispatch => {
  return {
      onAuth: (first_name, email, password, re_password) => dispatch(signup(first_name, email, password, re_password)) 
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Registration);