import React,{useEffect} from 'react';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { fetchCart } from "../../../store/actions/cart";
import styled from 'styled-components';

const Ul = styled.ul`
  @media (max-width: 830px) {
    flex-flow: column nowrap;
    background-color: #1F1D29;
    position: fixed;
    transform: ${({ open }) => open ? 'translateX(0)' : 'translateX(100%)'};
    top: 0;
    right: 0;
    height: 100vh;
    width: 250px;
    padding-top: 3rem;
    z-index: 1;
    transition: transform 0.3s ease-in-out;
    li {
      color: #fff;
      margin: 10px;
    }
  }
`;


const RightNav = ({ open, isAuthenticated, fetchCart }) => {
  useEffect(() => {
    fetchCart()
  },[fetchCart])
  return (
        <Ul open={open}>
        <li><Link to="/gamingpc">gaming pc</Link></li>
        <li><Link to="/pccomponents">Pc Components</Link></li>
        <li><Link to="/custom">Build Your PC</Link></li>
        <li><Link to="/aboutus">About us</Link></li>
        {
                    isAuthenticated ?
                    <li><Link to="/account">Account</Link></li>
                    :
                    <li><Link to="/login">login</Link></li>
        }
        </Ul>
  )
}

const mapStateToProps = state => {
    return {
      isAuthenticated: state.auth.isAuthenticated,
      cart: state.cart.shoppingCart
    }
  }
  const mapDispatchToProps = dispatch => {
    return {
      fetchCart: () => dispatch(fetchCart())
    }
  }
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RightNav))